import './MainBlockHeaderItemSettings.css';
import icon_settings_white from '../../../../foto/settings_white.png';
import icon_settings_black from '../../../../foto/settings_black.png';
import {NavLink} from "react-router-dom";

export const MainBlockHeaderItemSettings = () => {
  return (
    <NavLink to='/settings' className='Main-Block-Header-Item-Settings'>
      <img
        src={icon_settings_white}
        alt='settings'
      />
    </NavLink>
  );
};