import {ApiAuth} from "../../api/api";
import {setIsAuth, setUserData,} from "../reducers/auth";

export const ThunkSetIsAuth = () => {
  return async (dispatch) => {
    let response = await ApiAuth.checkIsAuth();
    response = response.data;

    if(response.resultCode === 0) {
      dispatch(setIsAuth(true));
      dispatch(setUserData({
        id: response.data.id,
        login: response.data.login,
        email: response.data.email,
      }));
    } else
    if(response.resultCode === 1) {
      dispatch(setIsAuth(false));
      dispatch(setUserData({
        id: null,
        login: null,
        email: null,
      }));
    };
  };
};

export const ThunkSetLogin = (email, password) => {
  return async (dispatch) => {
    let response = await ApiAuth.setLogin(email, password);
    switch (response.data.resultCode) {
      case 0:
        dispatch(setIsAuth(true));
        dispatch(ThunkSetIsAuth());
        break;
      case 1:
        dispatch(setIsAuth(false));
        break;
      case 10:
        dispatch(setIsAuth(false));
        break;
      default: break;
    };
  };
};

export const ThunkSetLogout = () => {
  return async (dispatch) => {
    const response = await ApiAuth.setLogout();
    console.log(response)
  };
};
