import './MainBlock.css';
import { Swiper, SwiperSlide } from "swiper/react";
import "swiper/css";
import "swiper/css/navigation";
import {useDispatch, useSelector} from "react-redux";
import {setActiveSlide} from "../../../redux/reducers/homeSwiper";
import {useEffect, useState} from "react";

export const MainBlock = () => {
  const activeSlide = useSelector((state) => state.homeSwiper.activeSlide);
  const dispatch = useDispatch();

  const onSetActiveSlide = activeIndex => dispatch(setActiveSlide(activeIndex));

  const [swiperRef, setSwiperRef] = useState(null);

  useEffect(() => {
    swiperRef !== null && swiperRef.slideTo(activeSlide, 300);
  }, [activeSlide]);

  return (
    <Swiper
      onSwiper={setSwiperRef}
      onSlideChange={(Swiper) => onSetActiveSlide(Swiper.activeIndex)}
      initialSlide={activeSlide}
      resistanceRatio={0}
      speed={300}
      className='swiper-home-main'
    >
      <SwiperSlide className='home-main-block'>Slide 1</SwiperSlide>
      <SwiperSlide className='home-main-block'>Slide 2</SwiperSlide>
    </Swiper>
  );
};