import {createSlice} from "@reduxjs/toolkit";

const initialState = {
  activeSlide: 1,
}

const HomeSwiper = createSlice({
  name: 'HomeSwiper',
  initialState,
  reducers: {
    setActiveSlide (state, action) {
      state.activeSlide = action.payload;
    },
  }
});

export const {
  setActiveSlide
} = HomeSwiper.actions;

export default HomeSwiper.reducer;