import './App.css';
import {TodoBlock} from "./Component/TodoBlock/TodoBlock";
import {Route, Routes} from "react-router-dom";
import {LoginPage} from "./Component/LoginPage/LoginPage";
import AuthHoc from "./HOC/authHoc";
import {useEffect} from "react";
import {ThunkSetIsAuth} from "./redux/thunks/Auth";
import {useDispatch} from "react-redux";
import {SettingsPage} from "./Component/SettingsPage/SettingsPage";
import {HomePage} from "./Component/HomePage/HomePage";

export const App = () => {
  const dispatch = useDispatch();

  let AuthTodoBlock = AuthHoc(TodoBlock);
  let AuthSettingsPage = AuthHoc(SettingsPage);

  useEffect(() => {
    dispatch(ThunkSetIsAuth());
  });

  return (
    <div className='App'>
      <Routes>
        <Route path='/login' element={<LoginPage context={'login'} />}/>
        <Route path='/registration' element={<LoginPage context={'registration'} />}/>
        <Route path='/settings' element={<AuthSettingsPage />} />
        <Route path='/todo' element={<AuthTodoBlock />}/>
        <Route path='/main' element={ <HomePage />}/>
      </Routes>
    </div>
  );
};