import React from 'react';
import {connect} from "react-redux";
import {ThunkSetIsAuth} from "../redux/thunks/Auth";
import {Navigate} from "react-router-dom";

const AuthHoc = (Component) => {

  class AuthHocContainer extends React.Component {
    componentDidMount() {
      this.props.ThunkSetIsAuth();
    };
    render () {
      if(this.props.isAuth === null) return null;
      return this.props.isAuth ? <Component /> : <Navigate to="/login" />
    };
  }
  let mapStateToProps = (state) => {
    return {
      isAuth: state.auth.isAuth,
    };
  };

  return connect(mapStateToProps, {ThunkSetIsAuth})(AuthHocContainer);
};

export default AuthHoc;