export const validateSingIn = (values) => {

  let errorsSingIn = {}

  if(!values.password) errorsSingIn.password = 'Enter a password';

  if(!values.email) {errorsSingIn.email = 'Enter a email'} else
  if (!/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i.test(values.email)) {
    errorsSingIn.email = 'Invalid email address';
  };

  // if(wrongLoginAuth) {
  //   errorsSingIn.email = 'lol';
  //   errorsSingIn.password = 'lol';
  // }

  return errorsSingIn;
};

export const validateSingUp = (values) => {
  let errorsSingUp = {};

  if(!values.name) errorsSingUp.name = 'Enter a name';
  if(!values.password) errorsSingUp.password = 'Enter a password';
  if(!values.password_2) errorsSingUp.password_2 = 'Repeat password';

  if(!values.email) {errorsSingUp.email = 'Enter a email'} else
  if (!/^[A-Z0-9._%+-]+@[A-Z0-9.-]+\.[A-Z]{2,}$/i.test(values.email)) {
    errorsSingUp.email = 'Invalid email address';
  };

  return errorsSingUp;
}