import './MainBlockHeader.css';
import {MainBlockHeaderItemSelect} from "./MainBlockHeaderItemSelect/MainBlockHeaderItemSelect";
import {MainBlockHeaderItemSettings} from "./MainBlockHeaderItemSettings/MainBlockHeaderItemSettings";
import icon_header_1 from '../../../foto/icon_header_1.png';
import icon_header_2 from '../../../foto/icon_header_2.png';
import {useSelector} from "react-redux";

export const MainBlockHeader = () => {

  const activeSlide = useSelector((state) => state.homeSwiper.activeSlide);
  const checkActive = num => activeSlide === num;

  return (
    <div className='Main-Block-Header'>
      <div className='Main-Block-Header-center'>
        <MainBlockHeaderItemSelect
          active={checkActive(0)}
          icon={icon_header_2}
          number={0}
        />
        <MainBlockHeaderItemSelect
          active={checkActive(1)}
          icon={icon_header_1}
          number={1}
        />
      </div>
      <MainBlockHeaderItemSettings />
    </div>
  );
};

