import './SingIn.css';
import '../SingStyle.css';
import {NavLink} from "react-router-dom";
import {Form, Formik} from "formik";
import {InputText} from "../InputValidate/InputText/InputText";
import {InputPassword} from "../InputValidate/InputPassword/InputPassword";
import {validateSingIn} from "../InputValidate/validate";
import {useEffect} from "react";
import {ThunkSetIsAuth, ThunkSetLogin} from "../../../redux/thunks/Auth";
import {useDispatch} from "react-redux";

export const SingIn = () => {

  const dispatch = useDispatch();

  const submitSingIn = (values) => {
    dispatch(ThunkSetLogin(values.email, values.password));
    values.captcha = '';
  };

  useEffect(() => {
    // dispatch(ThunkSetIsAuth())
  })

  let buttonAuthDisable = false;

  return (
    <div className='Sing-In Sing'>
      <p className='Name-Form'>Login</p>
      <Formik
        initialValues={{
          email: '',
          password: '',
        }}
        onSubmit={submitSingIn}
        validate={validateSingIn}
      >
        {({
            errors,
            touched,
          }) => {

          // errors = errorAuth(errors, wrongLoginAuth);

          return (
            <Form className='Sing'>
              <InputText
                errors={errors.email}
                touched={touched.email}
                name='email'
                type='email'
                placeholder='email'
              />
              <InputPassword
                errors={errors.password}
                touched={touched.password}
                name='password'
                type='password'
                placeholder='password'
              />
              <button
                className='btn-submit'
                type="submit"
                disabled={buttonAuthDisable}
              >Войти</button>
            </Form>
          )
        }}
      </Formik>
      <NavLink
        className='Name-Redirect'
        to='/registration'
      >Registration</NavLink>
    </div>
  );
};